function throttle(fn, interval) {
    let pre = 0
    return function() {
        const _this = this
        const arg = arguments
        let now = Date.now()
        if (now - pre > interval) {
            fn.apply(_this, arg)
            this.$message.success('httpsËØ∑Ê±ÇÊàêÂäü')
                // console.log(pre, now, now - pre)
            pre = now
        } else {
            this.$message.error('5sÂÜÖÂè™ËÉΩÊèê‰∫§‰∏ÄÊ¨°')
        }

    }
}
export { throttle }