Vue.use(Vuex)
import rsa from '../utils/rsa'
import random from '../utils/random'

export default new Vuex.Store({
    state: {
        hasHttpsRequest: false,
        //ÊúçÂä°Á´ØÊîØÊåÅÁöÑtslÁâàÊú¨
        versionMatch: {
            '1.2': true
        },
        cipherSuite: 'RSA_WITH_AES_128_GCM_SHA1', //‰∏∫‰∫ÜÊ®°ÊãüÊñπ‰æø,ÂØÜÁ†ÅÂ•ó‰ª∂ÂÜôÊ≠ª‰∏∫ËøôÁßç(ÂÜôÂÖ®ü¶à‰∫ÜÊàëba)
        clientVersion: undefined, //ÂÆ¢Êà∑Á´ØÁöÑtslÁâàÊú¨
        clientRandom: undefined, //tlsÁ¨¨‰∏ÄÊ¨°Êè°ÊâãÂÆ¢Êà∑Á´Ø‰º†Êù•ÁöÑÈöèÊú∫Êï∞ 
        serverRandom: undefined, //tlsÁ¨¨‰∫åÊ¨°Êè°ÊâãÊúçÂä°Á´Ø‰∫ßÁîüÁöÑÈöèÊú∫Êï∞
        //tlsÁ¨¨‰∫åÊ¨°Êè°ÊâãÊúçÂä°Á´ØÁªôÂÆ¢Êà∑Á´ØÁöÑÊä•Êñá
        tlsSecond: {
            meta: { success: undefined, msg: '' },
            data: {
                dcInfo: 'ÂÖ¨Èí•,ÊåÅÊúâËÄÖ‰ø°ÊÅØ,Áî®ÈÄî,ÊúâÊïàÊúüÁ≠â', //Êï∞Â≠óËØÅ‰π¶ ËøôÈáåÊëòË¶ÅÂ∞±Èöè‰æøÂÜôÂÜô
                dcHash: '', //Êï∞Â≠óËØÅ‰π¶ÁöÑhashÂÄº(caÂØπÊï∞Â≠óËØÅ‰π¶ÁöÑ‰ø°ÊÅØËøõË°åÂìàÂ∏åËÆ°ÁÆó ÂØÜÁ†ÅÂ•ó‰ª∂Áî®ÁöÑSHA256)
                cryptographicDcHash: '', //caÁî®Ëá™Â∑±ÁßÅüîëÂä†Á≠æ ÁîüÊàêÊï∞Â≠óÁ≠æÂêç
            },

        },
        tlsThird: {
            meta: {
                success: undefined,
                msg: '',
            }
        },
        //Ê®°Êãü‰∏ãtlsÊè°Êâã‰∫ßÁîüÁöÑÊä•Êñá,ÂÆ¢Êà∑Á´ØÁî®ÊëòË¶ÅÁÆóÊ≥ïÂ∞ÜÊä•ÊñáÂèòÊàêhashÂíå‰ºöËØùÂØÜÈí•(‰∏â‰∏™ÈöèÊú∫Êï∞rsaÂä†ÂØÜÂêé)ÁöÑÁªìÊûúÁªôÊúçÂä°Á´Ø
        //ÁÑ∂ÂêéÊúçÂä°Á´ØÂÜçÂ∞ÜÊú¨Âú∞ÁöÑÊä•ÊñáÂèòÊàêhash ÂÜçÈÄöËøáÂæóÂà∞ÁöÑrsaüîêÁöÑpreMasterÁîüÊàê‰ºöËØùÂØÜÈí• Á¨¨ÂõõÊ¨°ü§ùÂà§Êñ≠‰∏§ËÄÖÊòØÂê¶Áõ∏Âêå 
        totalMessage: undefined,
        masterKey: undefined,
        changeCipherSpec: undefined,
        encryptHandShakeMsg: undefined,
        httpResponse: {
            code: undefined,
            method: undefined,
            msg: '',
        }
    },
    mutations: {

        //tlsÂÆ¢Êà∑Á´ØÁ¨¨‰∏ÄÊ¨°Êè°ÊâãÂêé ÊúçÂä°Á´ØÊé•ÂèóÂÆ¢Êà∑Á´ØÂèëÈÄÅÁöÑtlsÁâàÊú¨Âè∑,ÈöèÊú∫Êï∞Á≠âÂèÇÊï∞,ÊúçÂä°Á´ØËøõË°å‰øùÂ≠ò
        tlsFirst(state, { version, random }) {
            state.clientRandom = random
            state.clientVersion = version
        },

        //tlsÊúçÂä°Á´ØÂèëËµ∑Á¨¨‰∫åÊ¨°Êè°Êâã,Âà§Êñ≠ÊòØÂê¶ÊîØÊåÅÂÆ¢Êà∑Á´ØÁöÑtlsÁâàÊú¨,Âπ∂Â∞ÜËØÅ‰π¶‰∫§ÁªôÂÆ¢Êà∑Á´Ø
        tlsSecond(state) {
            if (state.versionMatch[state.clientVersion]) {
                state.tlsSecond.meta.success = true //ËØ¥ÊòéÊúçÂä°Á´ØÊîØÊåÅÂÆ¢Êà∑Á´ØÁöÑtlsÁâàÊú¨
                state.serverRandom = random.createRandom() //ÁîüÊàêÊúçÂä°Á´ØÈöèÊú∫Êï∞
                state.tlsSecond.meta.msg = 'ÊúçÂä°Á´ØÊî∂Âà∞Á¨¨‰∏ÄÊ¨°ü§ù,ÊîØÊåÅËØ•browserÁöÑtlsÁâàÊú¨,ÂàÜÈÖçÂØÜÁ†ÅÂ•ó‰ª∂RSA_WITH_AES_128_GCM_SHA1 ÂèëËµ∑Á¨¨‰∫åÊ¨°Êè°Êâã ÂèëÈÄÅÊï∞Â≠óÁ≠æÂêç,ÂÖ¨üîëÁ≠âÁõ∏ÂÖ≥‰ø°ÊÅØ'
                state.tlsSecond.data.dcInfo = 'fake_dcinfofefesfsdscsvfdwfwfwfÁöÑËçâÂú∞‰∏äÂéïÊâÄ' //ca(ËØÅ‰π¶ËÆ§ËØÅÊú∫ÊûÑ)Á≠æÂèëÊï∞Â≠óÁ≠æÂêç
                state.tlsSecond.data.dcHash = sha1(state.tlsSecond.data.dcInfo) //sha1ÊëòË¶ÅÁÆóÊ≥ïÂä†ÂØÜdcInfo
                state.tlsSecond.data.cryptographicDcHash = rsa.signature(state.tlsSecond.data.dcHash) // caÁöÑÁßÅÈí•Âä†Á≠æhash
                state.totalMessage = 'ByteDance666'
            } else {
                //ËØ¥Êòé‰∏çÊîØÊåÅ ‰∏≠Ê≠¢ü§ù
                state.tlsSecond.meta.sucess = false
                state.tlsSecond.meta.msg = 'ÊúçÂä°Á´Ø‰∏çÊîØÊåÅbrowserÁöÑtlsÁâàÊú¨'
            }

        },
        tlsThird(state, { enc, encryptHandShakeMsg, changeCipherSpec }) {
            //Â∞ÜtotalMsg sha1ÊëòË¶Åhash 
            const hashTotalMsg = sha1(state.totalMessage)
                //ÁßÅüîëËß£ÂØÜenc ÂæóÂà∞preMaster ÁÑ∂ÂêéÂÜçÂæóÂà∞Ê≤°ÊúâüîêÁöÑrawMasterKey,ÁÑ∂ÂêéÂÜçüîêÂæóÂà∞MasterKey
            const preMaster = parseInt(rsa.decrypt(enc))
            const rawMasterKey = state.clientRandom + state.serverRandom + preMaster
            const masterKey = sha1(rawMasterKey)
            state.masterKey = masterKey
            state.encryptHandShakeMsg = sha1(state.totalMessage + masterKey)
                //Âà§Êñ≠ totalMsgHash+MasterKeyÊòØÂê¶‰∏éÂÆ¢Êà∑Á´ØÂèëÊù•ÁöÑencryptHandShakeMsgÁõ∏Âêå Á°Æ‰øùÊè°Êâã‰ø°ÊÅØÊú™Ë¢´ÁØ°Êîπ
            if (state.encryptHandShakeMsg == encryptHandShakeMsg) {
                state.tlsThird.meta.success = true
                state.tlsThird.meta.msg = 'ÊúçÂä°Á´ØÊî∂Âà∞Á¨¨‰∏âÊ¨°ü§ù‰ø°ÊÅØ ‰πãÂâçÁöÑü§ù‰ø°ÊÅØÊú™Ë¢´ÁØ°Êîπ Âí±‰ª¨ÂèØ‰ª•ËØ¥Âà´‰∫∫Áúã‰∏çÊáÇÁöÑhttp‰∫ÜÔºÅ'
            } else {
                state.tlsThird.meta.success = false
                state.tlsThird.meta.msg = 'ÊúçÂä°Á´ØÊî∂Âà∞Á¨¨‰∏âÊ¨°ü§ù‰ø°ÊÅØ ‰πãÂâçÁöÑü§ù‰ø°ÊÅØÂèØËÉΩË¢´ÁØ°Êîπ ÁªàÊ≠¢ÈÄö‰ø°'
            }
        },
        tlsForth(state) {
            //Á¨¨ÂõõÊ¨°ü§ù‰πüÂèë‰ºöËØùÂØÜÈí•Âä†ÂØÜÂêéÁöÑü§ù‰ø°ÊÅØÂíå ÂèØ‰ª•ÂºÄÂßãËØ¥Âà´‰∫∫Áúã‰∏çÊáÇÁöÑhttp‰∫Ü
            state.changeCipherSpec = true
        },
        http(state, payload) {
            //ÂÖàËß£ÂØÜ
            let msg = rsa.decrypt(payload)
                //Âà†ÂéªÊ≤°Áî®ÁöÑ‰ºöËØùÂØÜÈí•Â∞±ÊòØÊä•ÊñáÂÜÖÂÆπ

            const masterKeyIndex = msg.indexOf(state.masterKey)

            msg = msg.split('')

            msg.splice(masterKeyIndex, state.masterKey.length)
                //ËΩ¨Êç¢‰∏∫jsÂØπË±°
            msg = msg.join('')
            const { method, url } = JSON.parse(msg)
            if (url == 'https://juejin.cn/') {
                console.log('1')
                state.httpResponse.code = '200'
                state.httpResponse.method = method
                state.httpResponse.msg = `ÊúçÂä°Á´ØËøîÂõûËµÑÊ∫êÊàêÂäü,Áä∂ÊÄÅÁ†Å${state.httpResponse.code},Êî∂Âà∞ÁöÑËØ∑Ê±ÇÊñπÂºè${state.httpResponse.method}`
            } else {
                state.httpResponse.code = '404'
                state.httpResponse.msg = 'Âè™ËÆæÁΩÆ‰∫Ühttps://juejin.cn/Ëøô‰∏™ËµÑÊ∫ê Êç¢Âà∞ÊéòÈáëÂ∞±Ê≠£Â∏∏ÂñΩ Áªô‰Ω†‰∏™Áä∂ÊÄÅÁ†Å404 '
            }
            //ÊúçÂä°Á´ØÁöÑÊä•Êñá‰πüË¶ÅÁî®‰ºöËØùÂØÜÈí•ËøõË°å‰∏ÄÁ≥ªÂàóÂä†ÂØÜ... Â∞±Âà∞ËøôÂëäÊÆµËêΩ

        }
    },
    actions: {},
    modules: {}
})