<template>
  <div>
    <el-form :inline="true">
      <el-form-item label="httpËØ∑Ê±ÇÊñπÂºè">
        <el-select v-model="httpOption.method" placeholder="httpËØ∑Ê±ÇÊñπÂºè">
          <el-option label="GET" value="GET"></el-option>
          <el-option label="SET" value="SET"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="httpsConnectionStart">Êü•ÁúãhttpsËØ∑Ê±Ç</el-button>
      </el-form-item>
    </el-form>
    <Log :msgList="msgList"></Log>
  </div>
</template>

<script>
import Log from "./Log";
import { throttle } from "../utils/throttle.js";
let sha1 = require("js-sha1");
export default {
  components: {
    Log,
  },
  props: ["requestUrl"],

  data() {
    return {
      hasTimeOut: 0, //Ê∂àÊÅØÂàóË°®ÈáåÊòØÂê¶ÊúâÂÆöÊó∂Âô®(ËÆ©Ê∂àÊÅØÂàóË°®ÁöÑÊõ¥Êñ∞ÂÖ∑ÊúâÂª∂Ëøü)
      logMsgQueue: [], //Â¶ÇÊûúÊúâÂÆöÊó∂Âô®,Ê∂àÊÅØÂä†Âà∞ËØ•ÈòüÂàóÈáåÔºåÊ≤°ÊúâÂÆöÊó∂Âô®‰∫ÜÂÜçÈÄíÂΩíÂ¢ûÊ∑ªÈòüÈ¶ñÊ∂àÊÅØ Áõ¥Âà∞ÈòüÂàó‰∏∫Á©∫
      isFirstHttpsRequest: true,
      msgList: [], //ÈÄö‰ø°Êó•ÂøóÊ∂àÊÅØÂàóË°®
      tlsVersion: "1.2",
      clientRandom: undefined, //ÂÆ¢Êà∑Á´ØÈöèÊú∫Êï∞
      serverRandom: undefined, //ÊúçÂä°Á´ØÈöèÊú∫Êï∞
      preMaster: undefined, //Á¨¨‰∏âÊ¨°Êè°ÊâãÁîüÊàêÁöÑÈöèÊú∫Êï∞ ÁÑ∂Âêé‰∏â‰∏™ÈöèÊú∫Êï∞Âä†ÂØÜÂæóÂà∞‰ºöËØùÂØÜÈí•
      masterKey: undefined, //‰ºöËØùÂØÜÈí•
      totalMessage: undefined, //Á¨¨‰∏âÊ¨°Êè°ÊâãÂâçÁöÑÊâÄÊúâÊä•Êñá
      cipherSuites: "RSA_WITH_AES_128_GCM_SHA1", //ÂØÜÁ†ÅÂ•ó‰ª∂
      code: undefined,
      httpOption: {
        method: "",
        url: this.requestUrl,
      },
    };
  },

  methods: {
    //ÂÖ•Âè£ÂáΩÊï∞ ËäÇÊµÅÂåÖË£π
    httpsConnectionStart: throttle(function () {
      if (!this.isFirstHttpsRequest) this.msgList = [];
      this.isFirstHttpsRequest = false;
      this.logAppend(`Â∑≤ÁªèËÆ∞ÂΩïÊú¨Ê¨°httpsÈÄö‰ø°Ê®°ÊãüÊµÅÁ®ã Êú¨Ê¨°ËØ∑Ê±ÇÂú∞ÂùÄ‰∏∫${this.requestUrl}`);
      this.tlsFirst();
      this.tlsSecond();
      if (this.$store.state.tlsSecond.meta.success) {
        this.tlsThird();
        this.tlsForth();
        this.httpRequest({ method: this.httpOption.method, url: this.requestUrl });
        this.msgList = [];
      } else return;
    }, 1000),
    toNewAddress() {
      this.httpsConnectionStart();
      this.$emit("toNewAddress");
    },
    //tls-first handshake
    tlsFirst() {
      this.clientRandom = this.$random.createRandom();
      //ÂÆ¢Êà∑Á´ØÂèëËµ∑Á¨¨‰∏ÄÊ¨°ü§ù,Â∞Üversion,randomÁ≠â‰ø°ÊÅØÊèê‰∫§ÁªôÊúçÂä°Á´Ø
      this.$store.commit("tlsFirst", {
        version: this.tlsVersion,
        random: this.clientRandom,
      });
      this.logAppend("ÂÆ¢Êà∑Á´ØÂèëËµ∑Á¨¨‰∏ÄÊ¨°tlsü§ùÊàêÂäü!Êèê‰æõserverRandom,ÂØÜÁ†ÅÂ•ó‰ª∂,tlsÁâàÊú¨‰ø°ÊÅØ");
    },

    //tls-second handshake
    tlsSecond() {
      //ÊúçÂä°Á´ØÂèëËµ∑Á¨¨‰∫åÊ¨°ü§ù,stateÂ≠òÂÇ®Êä•Êñá‰ø°ÊÅØ
      this.$store.commit("tlsSecond");
      this.serverRandom = this.$store.state.serverRandom;
      this.totalMessage = this.$store.state.totalMessage;
      this.logAppend(this.$store.state.tlsSecond.meta.msg);
    },

    //tls-third handshake
    tlsThird() {
      //Ê£ÄÊü•ËØÅ‰π¶ÊòØÂê¶ÊúâÊïà
      //--ÊµèËßàÂô®ÂêåÊ†∑ÁöÑÁÆóÊ≥ï(sha1)ÂØπdcËøõË°åÂä†ÂØÜÂæóÂà∞h1
      const h1 = sha1(this.$store.state.tlsSecond.data.dcInfo);
      //--ÂÜçÁî®ÂÖ¨üîëËß£Á≠æ
      const h2 = this.$store.state.tlsSecond.data.cryptographicDcHash;
      //-- Â¶ÇÊûúh1==h2 ËØÅ‰π¶ÊúâÊïàüê∂
      const isValid = this.$rsa.verify(h1, h2);
      if (isValid) {
        this.logAppend("ÂÆ¢Êà∑Á´ØÊî∂Âà∞ÊúçÂä°Á´ØÁ¨¨‰∫åÊ¨°Êè°ÊâãÁöÑ‰ø°ÊÅØ,ÁªèÈ™åËØÅÊï∞Â≠óËØÅ‰π¶ÊúâÊïà");
      } else {
        this.logAppend("Êï∞Â≠óËØÅ‰π¶Êó†Êïà");
        throw new Error("Êï∞Â≠óËØÅ‰π¶Êó†Êïà");
      }
      //ËØÅ‰π¶‰ø°‰ªªÈìæ --‰∏∫‰∫ÜÊ†πËØÅ‰π¶‰∏çËΩªÊòìÂ§±ÂÆà,‰∏ÄÂ±ÇÂ±ÇÁöÑÊù•ËØ∑Ê±ÇÂæóÂà∞Ê†πËØÅ‰π¶
      //-- ‰º™‰ª£Á†Å:while(currentDc.È¢ÅÂ∏ÉËÄÖ!=null){
      //currentCa=requestPreCa(currentDc.È¢ÅÂ∏ÉËÄÖ) //ÂêëÂΩìÂâçËØÅ‰π¶Á≠æÂèëÊú∫ÊûÑËØ∑Ê±Ç‰∏ä‰∏ÄÁ∫ßÁöÑÊï∞Â≠óËØÅ‰π¶
      //    }
      //if(currentDc.È¢ÅÂ∏ÉËÄÖ==‰ø°‰ªªÁöÑÊ†πCAÊú∫ÊûÑ){ËØÅ‰π¶ÂèØ‰ø°‰ªª}

      //Â¶ÇÊûúËØÅ‰π¶ÊúâÊïà‰∏î‰ø°‰ªªÈÇ£ÂÆ¢Êà∑Á´ØÂ∞±ÂáÜÂ§áÁ¨¨‰∏âÊ¨°ü§ù
      this.preMaster = this.$random.createRandom();
      //rsaÂÖ¨üîëÂä†ÂØÜpreMaster Á≠â‰ºöÂèëÁªôÊúçÂä°Á´Ø
      const enc = this.$rsa.encrypt(this.preMaster);
      //ÁîüÊàê‰ºöËØùÂØÜÈí•masterkey(‰∏â‰∏™ÈöèÊú∫Êï∞Èöè‰æøÊâæ‰∏™Âä†ÂØÜÁÆóÊ≥ïÂä†ÂØÜ ÊåâÁÖßÂØÜÁ†ÅÂ•ó‰ª∂Â∫îËØ•Áî®aesÂØπÁß∞üîê ËøôÈáåËøòÊòØÁî®sha1Ê®°Êãü‰∏Ä‰∏ã)
      const rawMasterKey = this.serverRandom + this.clientRandom + this.preMaster;
      const masterKey = sha1(rawMasterKey);
      this.masterKey = masterKey;
      //ÁîüÊàê‰ºöËØùÂØÜÈí•Âä†ÂØÜÁöÑü§ù‰ø°ÊÅØ
      const encryptHandShakeMsg = sha1(this.totalMessage + masterKey);
      //ÂºÄÂßãÂä†ÂØÜ‰∫§ÊµÅ
      const changeCipherSpec = true;
      //ÂºÄÂßãÁ¨¨‰∏âÊ¨°ü§ù
      this.logAppend(
        "ÂÆ¢Êà∑Á´ØÂèëËµ∑Á¨¨‰∏âÊ¨°ü§ù,ÂèëÈÄÅÂÖ¨üîëÂä†ÂØÜËøáÁöÑÁ¨¨‰∏â‰∏™ÈöèÊú∫Êï∞ÂíåÁî®‰ºöËØùÂØÜÈí•Âä†ÂØÜËøáÁöÑü§ù‰ø°ÊÅØÊëòË¶Å"
      );
      this.$store.commit("tlsThird", {
        enc,
        encryptHandShakeMsg,
        changeCipherSpec,
      });
      if (this.$store.state.tlsThird.meta.success) {
        this.logAppend(this.$store.state.tlsThird.meta.msg);
      } else {
        throw new Error(this.$store.state.tlsThird.meta.msg);
      }
    },
    tlsForth() {
      this.$store.commit("tlsForth");
      this.logAppend(
        "ÂÆ¢Êà∑Á´ØÊî∂Âà∞ÊúçÂä°Á´ØÁ¨¨ÂõõÊ¨°ü§ùÊä•Êñá(Êè°Êâã‰ø°ÊÅØÊëòË¶ÅÂíåÂºÄÂßãÂä†ÂØÜÈÄö‰ø°) ‰∏ãÈù¢ÂèØ‰ª•ÂºÄÂßãËøõË°åÂä†ÂØÜÁöÑhttpÈÄö‰ø°"
      );
    },
    httpRequest(httpRequestOption) {
      //Ê†πÊçÆoptionÁîüÊàêhttpÊä•ÊñáÔºàËΩ¨ÊàêjsonÂ≠óÁ¨¶‰∏≤ÔºâÂÜçÂä†‰∏ä ‰ºöËØùÂØÜÈí•  ÂÜçËøõË°årsaÂÖ¨Èí•Âä†ÂØÜ
      const httpRequestMsg = JSON.stringify(httpRequestOption);
      const _httpRequestMsg = httpRequestMsg + this.masterKey;
      const sendHttpRequestMsg = this.$rsa.encrypt(_httpRequestMsg);
      //ÂèëÈÄÅhttpËØ∑Ê±Ç
      this.$store.commit("http", sendHttpRequestMsg);
      this.logAppend("ÂÆ¢Êà∑Á´ØÂ∑≤ÁªèÂèëÈÄÅÂä†ÂØÜÁöÑhttpËØ∑Ê±Ç");
      this.logAppend(this.$store.state.httpResponse.msg);
    },
    logAppend(msg) {
      //Ê≤°ÊúâÂÆöÊó∂Âô®,ÈÇ£Â∞±ËØ¥Êòé‰πãÂâçÊ∂àÊÅØÂàóË°®ÂÖ®ÈÉ®Êõ¥Êñ∞ÂÆå‰∫ÜÊàñËÄÖÈ¶ñÊ¨°Êõ¥Êñ∞
      //ËÆæÁΩÆ‰∏Ä‰∏™2sÁöÑÂÆöÊó∂Âô®,ÁªìÊùüÂêéÊâßË°åÊ∂àÊÅØÂàóË°®Êõ¥Êñ∞
      //ÂÆöÊó∂Âô®Â≠òÂú®ÊúüÈó¥‰ºöÊúâÊ∂àÊÅØÂàóË°®Êõ¥Êñ∞ËØ∑Ê±Ç,ÂÆöÊó∂‰ªªÂä°ÂÆåÊàêÂêéÂÜçÈÄíÂΩíÊâßË°åÈòªÂ°ûÈòüÂàóÈáåÁöÑ‰ªªÂä°
      //ÈÄíÂΩíÁªìÊùüÊù°‰ª∂:ÈòªÂ°ûÈòüÂàó‰∏∫Á©∫
      if (!this.hasTimeOut) {
        this.hasTimeOut = setTimeout(() => {
          this.msgList.push(msg);
          this.hasTimeOut = 0;
          if (this.logMsgQueue.length > 0) {
            let _msg = this.logMsgQueue.shift();
            this.logAppend(_msg);
          }
        }, 2000);
      } else {
        //ÊúâÂÆöÊó∂Âô® ÂæóÁ≠âÂæÖÂÆöÊó∂‰ªªÂä°ÊâßË°åÂÆåÊàê,ÂÖ•ÈòüÂà∞ÈòªÂ°ûÈòüÂàó‰∏≠
        this.logMsgQueue.push(msg);
      }
    },
  },
};
</script>

<style style lang="scss"></style>
